import moment from 'moment'

export default{
	methods: {
		cleanDate(date = "-"){
			let split = date.split("T");
			return split[0];
		},
		downloadFile(filename){
			return process.env.VUE_APP_API_URL + "/api/file/get_file/name?fileName=" + escape(filename);
		},
		removeItems(items, array){
			let difference = array.filter(x => !items.includes(x));
			return difference;
		},
		formatDate(date){
			if(date){
				return moment(date).format('LL');
			}else{
				return "-";
			}
		}
	}
}